/* 기본 스타일 초기화 */
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: pretendard-regular;
  background-color: #282c34;
  color: white;
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow-x: hidden;
}

/* 상단 고정 섹션 */
#top-section {
  width: 100%;
  flex-shrink: 0; /* 상단 고정 */
  text-align: center;
  padding: 10px 0;
}

/* 선택적으로 스크롤 가능한 섹션 */
#todo-section {
  width: 100%;
  flex-grow: 1; /* 남은 공간 사용 */
  overflow-y: auto; /* Y축 스크롤 활성화 */
  padding: 20px;
  max-height: calc(100vh - 150px); /* 제한된 높이 (상단/하단 섹션 포함) */
  scrollbar-width: none; /* Firefox에서 스크롤바 숨기기 */
  -ms-overflow-style: none; /* IE에서 스크롤바 숨기기 */
}

#todo-section::-webkit-scrollbar {
  display: none; /* Webkit 기반 브라우저에서 스크롤바 숨기기 */
}

/* 하단 섹션 고정 */
#quote,
#weather {
  width: 100%;
  flex-shrink: 0; /* 고정 */
}

/* 시계 */
#clock {
  font-size: 96px;
  margin-bottom: 20px;
  font-weight: bold;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

/* 로그인 폼 */
#login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

#login-form input[type="text"] {
  width: 300px;
  padding: 10px;
  font-size: 16px;
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;
  outline: none;
}

#login-form input[type="submit"] {
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background-color: #007bff00;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

#login-form input[type="submit"]:hover {
  background-color: #0056b3;
}

/* 인사말 */
#greeting {
  font-size: 20px;
  margin-top: 20px;
}

.hidden {
  display: none;
}

/* 할 일 목록 */
#todo-form {
  margin-top: 30px;
}

#todo-form input[type="text"] {
  width: 400px;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  outline: none;
}

#todo-list {
  margin-top: 20px;
  list-style: none;
  padding: 0;
}

#todo-list li {
  margin: 10px 0;
  padding: 10px;
  font-size: 18px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  text-align: center;
}

#todo-section {
  width: 100%;
  max-height: calc(100vh - 200px); /* 상단과 하단의 고정 영역을 제외한 높이 */
  overflow-y: auto; /* Y축 스크롤 활성화 */
  padding: 20px;
  scrollbar-width: none; /* Firefox에서 스크롤바 숨기기 */
  -ms-overflow-style: none; /* IE에서 스크롤바 숨기기 */
}

#todo-section::-webkit-scrollbar {
  display: none; /* Webkit 기반 브라우저에서 스크롤바 숨기기 */
}

.app-blurred {
  filter: blur(5px);
  pointer-events: none;
}

/* 포커스 모드 토글 버튼 */
.focus-mode-toggle-button {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 30;
  padding: 10px 20px;
  background-color: #202020;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.focus-mode-toggle-button:hover {
  background-color: #515151;
}

#logout-button {
  margin-left: 10px;
  padding: 5px 10px;
  font-size: 14px;
  color: white;
  background-color: #adadad21;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

#logout-button:hover {
  background-color: #ededed21;
}

